<template>
	<div>
		<sg-card titulo="Resultado do questionrio" valor="100">
			<div>
				<h3>
					Seu resultado foi '{{ resultado }}'
				</h3>

				<div>
					<span class="txt">
						<div>Primeiramente, agradecemos a voc pela disposio e cooperao ao participar da nossa pesquisa, atravs da sua avaliao vem sendo possvel melhorar o trabalho desenvolvido. Diante do estudo desenvolvido conclui que  de suma importncia para o aprendizado, que o indivduo possua informaes suficientes para elaborar sua prpria maneira de estudo, tais caractersticas ficam explicitas nos seguintes perfis de aprendizagem: Auditivo, Sinestsico e/ou Visual, que foram identificados com a participao do pblico alvo de discentes no IFTM  Campus Ituiutaba, visando o melhor desempenho destes buscamos trazer dicas de estudo presentes em cada personalidade especfica.</div>
						<div> De acordo com o questionrio respondido, o perfil que prevalece em voc  o de aluno: <strong>{{resultado}}</strong>. Propomos que voc siga as orientaes citadas a seguir para que assim consiga explorar ao mximo sua prpria capacidade.</div>
						<div> interessante destacar que todas as pessoas possui os trs tipos de percepo, no entanto apenas um prevalece, sendo assim, fica ntida a necessidade do conhecimento sobre os trs meios.</div>
						<div>
							<strong>Alunos auditivos</strong> processam o contedo facilmente associando-o a recursos sonoros, algumas dicas podem facilitar no processo de compreenso, como:
							<ul>
								<li>Estudar em voz alta;</li>
								<li>Atribuir palavras chaves ao contedo;</li>
								<li>Associar o estudo a letras de msicas, ou mesmo pardias;</li>
								<li>Explorar gravaes de udio que lhe faam pensar sobre a matria;</li>
								<li>Participar debates poder ser extremante produtivo;</li>
							</ul>
						</div>

						<div>
							<strong>Alunos sinestsicos</strong> se desenvolvem melhor atravs de uma abordagem mais dinmica. Se movimentando, tocando e fazendo... Algumas sugestes de estudos podem ser vlidas:
							<ul>
								<li>Aulas proativas so timas para estudantes com esse perfil;</li>
								<li>Usar tcnicas de memorizao que envolva gestos;</li>
								<li>Realizar intervalos frequentes no decorrer do estudo;</li>
								<li>Sempre mudar o local e a posio em que se estuda;</li>
								<li>Explorar livros que tragam exemplos cotidianos;</li>
							</ul>
						</div>

						<div>
							Alunos Visuais se sentem mais confortveis em aprender fazendo uso da viso e seus respectivos recursos. Para usufrurem de todo seu potencial, h algumas instrues:
							<ul>
								<li>Escrever informaes que precisem se lembrar;</li>
								<li>Elaborar diagramas e infogrficos pessoais, transformando-os em esquemas;</li>
								<li>Explorar os mapas mentais;</li>
								<li>Realizar leituras silenciosas sobre o contedo em questo;</li>
								<li>Ao estudar, destacar trechos importantes;</li>
							</ul>
						</div>

					</span>

					<hr>

					<div class="formulario">
						<small><strong>Registre seu resultado para nos ajudar:</strong></small>

						<b-form-input v-model="nome" :state="nome.length?'success':'warning'"
							type="text" placeholder="Digite seu nome">
						</b-form-input>
						<br>
						<b-form-input v-model="email" :state="email.length?'success':'warning'"
							type="email" placeholder="Digite seu email">
						</b-form-input>
						<br>
					</div>
				</div>

				<b-button size="sm" variant="success" @click="enviar">
					Enviar resultado para o IFTM
				</b-button>
				<b-button size="sm" variant="success" @click="reiniciar">
					Reiniciar teste
				</b-button>
			</div>
		</sg-card>
	</div>
</template>
<script>
import SgCard from './Card.vue'

export default{
	name: 'sg-resultado',
	components: {SgCard},
	data() {
		return {
			resultado: {},
			nome: "",
			email: ""
		}
	},
	created() {
		let respostas = this.$store.state.respostas

		if(respostas.A > respostas.B && respostas.A > respostas.C)
			this.resultado = "Auditivo"
		else if(respostas.B > respostas.A && respostas.B > respostas.C)
			this.resultado = "Sinestsico"
		else if(respostas.C > respostas.A && respostas.C > respostas.B)
			this.resultado = "Visual"
		else if(respostas.A == respostas.B && respostas.A > respostas.C)
			this.resultado = "Auditivo e Sinestsico"
		else if(respostas.B == respostas.C && respostas.B > respostas.A)
			this.resultado = "Sinestsico e Visual"
		else if(respostas.A == respostas.C && respostas.A > respostas.B)
			this.resultado = "Auditivo e Visual"
		else
			this.resultado = "Auditivo, Sinestsico e Visual"

		localStorage.setItem('resultado', this.resultado)
	},
	methods: {
		reiniciar() {
			this.$router.replace('/')
		},
		enviar() {
			if(this.nome != '' && this.email != '') {
				let corpo = `${this.nome} com o email ${this.email} fez o teste e foi captado o resultado: ${this.resultado}`
				window.location = 'mailto:pesquisaxextensao@gmail.com?subject=[Q.PERFIL] Nova resposta&body=' + corpo
			}
		}
	}
}

</script>
<style>
	div.formulario {
		margin-left: auto;
		margin-right: auto;
		width: 80%;
	}
	span.txt {
		text-align: justify;
	}
	span.txt div {
		padding: 10px;
	}
</style>


